.user {
    &__add {
        max-width: 570px;
        margin: 0 auto;
        &-h1 {
            font-size: 34px;
            font-weight: normal;
            font-style: normal;
            font-stretch: normal;
            color: $head;
            text-align: center;
            
            margin-bottom: 30px;
            @media (max-width:600px) {
                margin-bottom: 10px;
                font-size: 24px;
            }
        }
        &-step {
            font-size: 24px;
            text-align: center;
            margin-bottom: 35px;
            @media (max-width:600px) {
                margin-bottom: 10px;
                font-size: 16px;
            }
            span {
                color: $pur;
            }
        }
        .jq-selectbox {
            width: 100%!important;
            max-width: 100%!important;
            &__select {
                height: 48px;
                font-size: 16px;
            }
        }
    }
    &__block {
        border-radius: 4px;
        box-shadow: 0 4px 8px 0 #0000001e;
        background-color: #ffffff;
        height: 100%;
        &-head {
            padding: 20px 30px 15px;
            font-size: 15px;
            line-height: 26px;
            border-bottom: 1px solid #e4ebe7; 
            @media (max-width:600px) {
                padding: 10px;
            }
            i {
                font-size: 16px;
                color: #808080;
            }
        }
        &-body {
            padding: 24px 30px;
            @media (max-width:600px) {
                padding: 10px;
            }
        }
    }
    &__info {
        margin-bottom: 15px;
    }
    &__lesson {
        &-number {
            font-family: $med;
            font-size: 13px;
            line-height: 1.77em;
            
            margin-bottom: 20px;
        }
        &-line {
            border-radius: 4px;
            overflow: hidden;
            color: $head;
            background: $bggrey;
            font-family: $med;
            margin-bottom: 20px;
            width: 100%;
            &-div {
                text-align: center;
                border-radius: 4px;
                width: 100%;
                min-height: 10px;
            }
            &-green {
                background: $green;
                color: #fff;
                width: auto;
            }
        }
        &-info {
            @media (max-width:600px) {
                flex-direction: column;
                .d-flex {
                    padding-top: 7px;
                }
            }
            .user__lesson-line {
                width: 16px;
                margin-right: 16px;
                margin-bottom: 0;
            }
            .d-flex {
                margin-right: 15px;
            }
        }
    }
}