@import "variables";
@import "mixins";

html,
body {
    margin: 0;
    padding: 0;
    background: $bg;
    font-family: $reg;
    font-size: 14px;
    height: 100%;
    min-height: 100%;
}
* {
     box-sizing: border-box;
}
.body {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    transition: transform ease 0.4s;
    &.menu_open {
        transform: translate(280px, 0px);
        overflow: hidden;
    }
}
.aside {
    width: 270px;
    min-width: 270px;
    box-shadow: 1px 0 0 0 rgb(234, 237, 243);
    background-color: rgb(255, 255, 255);
    
    flex-direction: column;
    @media (max-width:950px) {
        margin-left: -280px;  
        min-width: 270px;
    }
}
.section {
    padding: 60px;
    width: 100%;
    height: auto;
    overflow: auto;
    @media (max-width:960px) {
        padding: 20px;
    }
    @media (max-width:700px) {
        padding: 10px;
    }
    &__head {
        padding-bottom: 23px;
        font-family: $med;
        &-h1 {
            font-size: 24px;
            @media (max-width:600px) {
                margin-bottom: 10px;
            }
            .btn {
                font-size: 14px;
                margin-left: 15px;
                display: flex;
                align-items: center;
            }
        }
        .d-flex {
            flex-wrap: wrap;
        }
    }
    &-center {
        max-width: 570px;
        margin: 0 auto; 
        padding-top: 0;
        text-align: center;
        h1 {
            margin-bottom: 60px;
        }
        .h2 {
            font-size: 24px;
            max-width: 100%;
        }
    }
    &-log {
        padding-top: 0;
    }
}
.forgot_password {
    margin-bottom: 60px;
    font-size: 14px;
    i {
        margin-right: 7px;
        display: block;
        font-size: 12px;
    }
    a {
        color: #b8b9b8;
        text-decoration: none;
        text-transform: uppercase;
        &:hover {
            color: $pur;
        }
    }
}
form {
    .btn {
        text-align: center;
        &-pur {
            min-width: 200px;
        }
        
    }
}
label {
    cursor: pointer;
    .d-flex {
        align-items: center;
    }
    input{
        &[type="checkbox"] + span {
            margin-top: 0px;
        }
    }
}
.label {
    
    margin-bottom: 27px;
    display: block;
    &-name {
        color: $menu;
        font-size: 13px;
        margin-bottom: 7px;
    }
    &-pass {
        position: relative;
        i {
            position: absolute;
            z-index: 2;
            top: 50%;
            right: 15px;
            transform: translate(0,-50%);
            font-size: 14px;
            color: #b8b9b8;
        }
    }
}
input {
    &[type="search"],
    &[type="password"],
    &[type="text"] {
        border-radius: 4px;
        border: solid 1px #e4ebe7;
        background-color: #ffffff;
        width: 100%;
        padding: 16px 20px;
        font-family: $reg;
        position: relative;
        @include plhl() {
            color: $headbottom;
        }
    }
    &[type="checkbox"] {
        display: none;
        & + span {
            width: 16px;
            height: 16px;
            min-width: 16px;
            min-height: 16px;
            display: inline-block;
            border: 1px solid #e4ebe7;
            vertical-align: middle;
            margin-top: -2px;
            margin-right: 20px;
            margin-right: 8px;
            color: #fff;
            border-radius: 2px;
            font-size: 12px;
            text-align: center;
        }
        &:checked {
            + span {
                background: $pur;
                border-color: $pur;
                border-radius: 2px;
                &:after {
                    content: '\e942';
                    font-family: 'icomoon' !important;
                    speak: none;
                    font-style: normal;
                    font-weight: normal;
                    font-variant: normal;
                    text-transform: none;
                    line-height: 1;
                    -webkit-font-smoothing: antialiased;
                    -moz-osx-font-smoothing: grayscale;
                }
            }
        }
        &:hover {
            + span {
                border-color: $pur;
            }
        }
    }
    
}
.back {
    color: $head;
    text-decoration: none;
    font-size: 15px;
    margin-bottom: 30px;
    display: inline-flex;
    align-items: center;
    i {
        font-size: 21px;
        margin-right: 17.5px;
    }
}

.filter {
    color: $menu;
    margin-left: 15px;
    
    font-size: 27px;
    text-decoration: none;
    
    margin-left: 30px;
}
.search {
    &-input {
        position: relative;
        i {
            position: absolute;
            top: 50%;
            right: 15px;
            transform: translate(0,-50%);
            color: #b8b9b8;
            font-size: 17px;
        }
        input {
            font-size: 16px;
            @include plhl() {
                color: #808080;
            }
        }
    }
    i {
        z-index: 2px;
        position: absolute;
        top: 50%;
        right: 10px;
        
    }
}
.or {
    padding: 0 5px;
    display: inline-block;
}
.load {
    &-head {
        font-size: 15px;
        color: #242423;
        margin-bottom: 15px;
    }
    &-body {
        align-items: center;
        margin-bottom: 20px;
        font-size: 16px;
        @media (max-width:600px) {
            flex-wrap: wrap;
        }
    }
    &-left {
        position: relative;
        margin-right: 26px;
        input {
            position: absolute;
            width: 100%;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            z-index: 2;
            cursor: pointer;
            opacity: 0;
        }
        span {
            position: relative;
            z-index: 1;
            color: #242423;
            border: 1px solid #808080;
            
        }
    }
}
.mobile {
    display: flex;
        @media (min-width:951px) {
            display: none;
        }
    .logo {
        padding: 0;
        
    }
    margin-bottom: 20px;
}
@media (min-width: 576px) {
    .container {
        max-width: 100%;
    }
}

.table-tab {
    
}
h1 {
    text-align: center;
    font-weight: normal;
    font-size: 34px;
    @media (max-width:600px) {
        font-size: 24px;   
    }
    p {
        padding: 0;
        font-size: 15px;
        color: #242423;
        max-width: 405px;
        display: block;
        margin: 7px auto;
    }
}

.end {
    max-width: 680px;
    margin: 0 auto;
    &_p {
        text-align: center;
        font-size: 24px;
          font-weight: normal;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.25;
          letter-spacing: normal;
          text-align: center;
          color: #242424;
        margin-bottom: 25px;
        @media (max-width:600px) {
            margin-bottom: 15px;   
            font-size: 18px;
        }
    }
    .img {
            width: 100%;
            height: auto;
            display: block;
            margin-bottom: 45px;
        @media (max-width:600px) {
            margin-bottom: 15px;   
        }
        }
    .d-flex {
        @media (max-width:600px) {
            flex-wrap: wrap;
            .btn {
                min-width: 100%;
                margin-bottom: 10px;
                text-align: center;
            }
        }
    }
}

.container_ {
    max-width: 1050px;
//    margin: 0 auto;
    
}
.colo {
    word-break: break-all;
}
