.table {
    border: solid 1px rgb(234, 237, 243);
    background-color: rgb(255, 255, 255);
    &-tabs {
        &-a {
            font-family: $reg;
            font-size: 15px;
            
            line-height: 1.6;
            letter-spacing: normal;
            text-align: center;
            color: $deactive;
            padding: 13px;
            text-decoration: none;
            border-bottom: 3px solid $headbottom;
            display: flex;
            align-items: center;
            justify-content: center;
            &.active {
                font-family: $med;
                color: $head;
                border-bottom-color: $pur;
            }
        }
    }
    &-head {
        
        font-family: $med;
        font-size: 12px;
        @media (max-width:600px) {
            display: none!important;
        }
        .col {
            min-height: 62px;
            display: flex;
            align-items: center;

        }
        .colo {
            min-height: 62px;
            display: flex;
            align-items: center;
//            padding: 23px 10px;
                &:nth-child(1) {
                    width: 32.3%;
                    padding-left: 2.857%;
                    @media (max-width:600px) {
                        padding-left: 50%;
                    }
                }
                &:nth-child(2),
                &:nth-child(3) {
                    width: 29.7%;
                    @media (min-width:601px) {
                        padding-right: 0.5%;
                    }
        
                }
                &:nth-child(4) {
                    width: 8%;
                }
        }
        
        &-sort {
            i {
                color: #808080;
                font-size: 8px;
                margin-left: 10px;
                    margin-top: 3px;
            }
            .col {
                cursor: pointer;
                display: flex;
                align-items: center;
                
            }
        }
        > .d-flex {
            border-bottom: 1px solid $tebleborder;
        }
        &-list {
            .colo {
                &:nth-child(1) {
                    width: 32%;
                }
                &:nth-child(2) {
                    width: 18%;
                }
                &:nth-child(3) {
                    width: 15.5%;
                }
                &:nth-child(4) {
                    width: 18.76%;
                }
                &:nth-child(5) {
                    width: 15%;
                }
            }
        }
        &-t {
            .colo {
                min-height: 50px;
                    &:nth-child(1) {
                        width: 28.38%;
                        padding-left: 2.857%;
                        @media (max-width:600px) {
                            padding-left: 50%;
                        }
                    }
                &:nth-child(2) {
                    width: 17.333%
                }
                &:nth-child(3) {
                    width: 13.523%
                }
                &:nth-child(4),
                &:nth-child(5) {
                    width: 15.04%
                }
                &:nth-child(6) {
                    width: 10.75%
                }
            }
        }
    }
    &-body {
        color: $main;
        font-size: 13px;
        .col {
            padding: 0 10px;
            min-height: 50px;
            align-items: center;
            display: flex;
            padding-top: 2px;
            padding-bottom: 2px;
            @media (max-width:600px) {
                flex: 0 0 100%!important;
                max-width: 100%!important;
                justify-content: flex-start;
                padding-left: 50%;
                position: relative;
                min-height: 35px;
                flex-wrap: wrap;
                
                
                
                .or {
                    width: 100%;
                    padding: 5px;
                }
                &:before {
                    content: attr(data-text);
                    position: absolute;
                    left: 0;
                    width: 50%;
                    top: 0;
                    bottom: 0;
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;
                    padding-right: 15px;
                    font-family: $med;
                }
                &.edit,
                &:last-child {
                    justify-content: center;
                    padding-left: 10px;
                    padding-right: 10px;
                    &:before {
                        display: none;
                    }
                }
                &.edit {
                    
                    .or {
                        width: auto;
                    }
                }
            }
//            &:last-child {
//                display: flex;
//                justify-content: flex-end;
//                align-items: center;
//            }
            &o {
                min-height: 50px;
                display: flex;
                align-items: center;
                padding-top: 2px;
                padding-bottom: 2px;
                &:nth-child(1) {
                    width: 32.3%;
                    padding-left: 2.857%;
                    @media (max-width:600px) {
                        padding-left: 50%;
                    }
                }
                &:nth-child(2),
                &:nth-child(3) {
                    width: 29.7%;
                }
                &:nth-child(4) {
                    width: 8%;
                }
                @media (max-width:600px) {
                    
                    flex: 0 0 100%!important;
                    max-width: 100%!important;
                    justify-content: flex-start;
                    padding-left: 50%;
                    position: relative;
                    min-height: 35px;
                    flex-wrap: wrap;


                    
                    .or {
                        width: 100%;
                        padding: 5px;
                    }
                    &:before {
                        content: attr(data-text);
                        position: absolute;
                        left: 0;
                        width: 50%;
                        top: 0;
                        bottom: 0;
                        display: flex;
                        justify-content: flex-end;
                        align-items: center;
                        padding-right: 15px;
                        font-family: $med;
                    }
                    &.edit,
                    &:last-child {
                        justify-content: center;
                        padding-left: 10px;
                        padding-right: 10px;
                        &:before {
                            display: none;
                        }
                    }
                    &.edit {

                        .or {
                            width: auto;
                        }
                    }
                }
            }
        }
        .d-flex:not(.user__lesson-line) {
            position: relative;
            @media (max-width:600px) {
                padding-top: 12px;
                padding-bottom: 12px;
                flex-wrap: wrap;
            }
            
            & + .d-flex {
                border-top: 1px solid $tebleborder;
            }
        }
        .user__lesson-line {
            margin-bottom: 0;
            margin-right: 7px;
        }
        .btn {
            width: 100%;
            text-align: center;
            padding-left: 0;
            padding-right: 0;
        }
        a[class^="icon-"], 
        a[class*=" icon-"] {
            color: $headbottom;
            font-size: 18px;
            text-decoration: none;
            & + a {
                margin-left: 17px;
            }
            &:hover {
                color: $pur;
            }
        }
        &-bg {
            > .d-flex {
                background: rgba(234, 237, 243, 0.3);
                &:nth-child(2n) {
                    background: none;
                }
            }
            .col {
                min-height: 70px;
                @media (max-width:600px) {
                    min-height: 30px;
                }
            }
        }
        &-last {
            .colo {
                @media (max-width:600px) {
                    &:last-child {
                        padding-left: 50%;
                        justify-content: flex-start;
                        &:after {
                            content: attr(data-text);
                            position: absolute;
                            left: 0;
                            width: 50%;
                            top: 0;
                            bottom: 0;
                            display: flex;
                            justify-content: flex-end;
                            align-items: center;
                            padding-right: 15px;
                            font-family: $med;
                        }
                    }
                    &:first-child {
                        justify-content: center!important;
                        padding-left: 0!important;
                        &:before {
                            display: none;
                        }
                    }
                }
            }
        }
        &-list {
            .colo {
                
                min-height: 69px;
                @media (max-width:600px) {
                    min-height: 24px;
                }
                &:nth-child(1) {
                    width: 32%;
                    @media (max-width:600px) {
                        justify-content: center;
                        padding-left: 0!important;
                    }
                    
                }
                &:nth-child(2) {
                    width: 18%;
                }
                &:nth-child(3) {
                    width: 15.5%;
                }
                &:nth-child(4) {
                    width: 18.76%;
                    flex-wrap: nowrap;
                    font-weight: 700;
                    word-break: inherit;
                }
                &:nth-child(5) {
                    width: 15%;
                }
                
                
                
                & {
                    @media (max-width:600px) {
                        justify-content: center;
                    }
                    padding-left: 10px;
                    padding-right: 10px;
                    &:before {
                        display: none;
                    }
                }
                
            }
        }
        &-t {
            .colo {
                min-height: 68px;
                @media (max-width:600px) {
                    min-height: 24px;
                    width: 100%!important
                }
                    &:nth-child(1) {
                        width: 28.38%;
                        padding-left: 2.857%;
                        @media (max-width:600px) {
                            padding-left: 50%;
                        }
                    }
                &:nth-child(2) {
                    width: 17.333%
                }
                &:nth-child(3) {
                    width: 13.523%
                }
                &:nth-child(4),
                &:nth-child(5) {
                    width: 15.04%
                }
                &:nth-child(6) {
                    width: 10.75%
                }
            }
        }
    }
}