.skill {
    display: flex;
    flex-wrap: wrap;
    
    &-item {
        border-radius: 4px;
        border: solid 1px #979797;
        color: $menu;
        padding: 8px 16px;
        text-transform: uppercase;
        
        font-size: 14px;
        
        align-items: center;
        display: inline-flex;
        
        margin: 0 20px 20px 0;
        i {
            cursor: pointer;
            font-size: 20px;
            margin-left: 8px;
        }
    }
}