.logo {
    padding: 30px;
    max-width: 160px;
    img,
    svg {
        width: 100%;
    }
    &-log {
        max-width: 150px;
        display: block;
        margin: 0 auto;
        img {
            width: 100%;
        }
    }
}
.nav {
    padding: 10px 0;
    flex-direction: column;
    
    &-a {
        width: 100%;
        padding: 12px 10px 12px 30px;
        border-left: 3px solid rgba(255,255,255,0);
        
        font-family: $reg;
        text-decoration: none;
        font-size: 14px;
        color: $menu;
        margin-bottom: 1px;
        i {
            font-size: 18px;
        }
        &.active {
            font-family: $med;
            border-left-color: $pur;
            color: $pur;
            background: rgba(174, 0, 255, 0.02);
        }
        &:hover {
            background: rgba(174, 0, 255, 0.02);
        }
    }
    i {
        margin-right: 20px;
    }
}