.btn {
    text-decoration: none;
    font-family: $med;
    border-radius: 4px;
    padding: 7px 16px;
    transition: $tr;
    &-pur {
        background: $pur;
        display: block;
        border: 1px solid $pur;
        background-color: $pur;
        padding: 7px 16px;
        text-transform: uppercase;
        color: #fff;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12);
        &:hover {
            box-shadow: none;
            background: $purh;
        }
        &:active {
            background: #9500db;
        }
        i {
            margin-right: 7px;
        }
    }
    
    &-check {
        width: 32px;
        min-width: 32px;
        height: 32px;
        min-height: 32px;
        max-width: 32px;
        max-height: 32px;
        background: $pur;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        color: #fff;
        font-size: 15px;
    }
    &-big {
        padding: 14px 38px;
        font-size: 16px;
        text-transform: uppercase;
        display: inline-block;
        &-text {
            text-transform: uppercase;
        }
    }
    &-white {
        color: $grey;
        border: 1px solid $menu;
        &:hover {
            border-color: #b8b9b8;
        }
        &:active {
            border-color: #000;
        }
        &-pur {
            border: 1px solid $pur;
            color: $pur;
            &:hover {
                color: #fff;
                background: $pur;
            }
        }
    }
    &-mob {
        width: 30px;
        height: 30px;
        margin-right: 15px;
        position: relative;
        cursor: pointer;
        span {
            position: absolute;
            top: 50%;
            width: 28px;
            height: 4px;
            background: $pur;
            transform: translate(0,-50%);
            transition: $tr;
            &:after,
            &:before {
                content: '';
                display: block;
                position: absolute;
                width: 30px;
                height: 4px;
                background: $pur;
                transition: $tr;
            }
            &:after {
                bottom: -8px;
            }
            &:before {
                top: -8px;   
            }
        }
        &.menu_open {
            span {
                background: none;
                
                &:after {
                    bottom: 0px;
                    transform: rotate(45deg)
                }
                &:before {
                    top: 0px;   
                    transform: rotate(-45deg)
                }
            }
            & + .logo {
                opacity: 0;
            }
        }
    }
}